import types from '../types/types';

const initialState = {
    events: [],
    active_event: null,
};

const eventReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.EVENT_NEW:
            return {
                ...state,
                events: [action.payload, ...state.events],
            };
        case types.EVENT_LOAD:
            return { ...state, events: [...action.payload] };
        case types.EVENT_UPDATE:
            return {
                ...state,
                events: state.events.map(event =>
                    event.id === action.payload.id ? action.payload : event
                ),
            };
        case types.EVENT_DELETE:
            return {
                ...state,
                events: state.events.filter(
                    event => event.id !== state.active_event.id
                ),
                active_event: null,
            };
        case types.EVENT_SET_ACTIVE:
            return {
                ...state,
                active_event: action.payload,
            };
        case types.EVENT_CLEAR_ACTIVE_EVENT:
            return {
                ...state,
                active_event: null,
            };
        case types.EVENT_LOGOUT:
            return { ...initialState };

        default:
            return state;
    }
};

export default eventReducer;
