import types from '../types/types';

const initialState = { modal_open: false };

const uiReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.UI_OPEN_MODAL:
            return { ...state, modal_open: true };
        case types.UI_CLOSE_MODAL:
            return { ...state, modal_open: false };
        case types.UI_SET_ERROR:
            return {
                ...state,
                error_msg: action.payload.errorMsg,
                input_name: action.payload.inputName,
            };

        default:
            return state;
    }
};

export default uiReducer;
