import Swal from 'sweetalert2';

import types from '../types/types';

import { fetchDataWithJWT, fetchDataWithoutJWT } from '../helpers/fetchData';

import { eventLogout } from './eventActions';

export const startLogin = (email, password) => async dispatch => {
    const response = await fetchDataWithoutJWT(
        'auth',
        { email, password },
        'POST'
    );

    const body = await response.json();
    const { ok, token, name, u_id } = body;

    if (ok) {
        localStorage.setItem('token', token);
        localStorage.setItem('token-init-date', new Date().getTime());

        dispatch(login({ name, u_id }));
    } else Swal.fire('Error', body?.msg, 'error');
};

export const startRegister = (user_name, email, password) => async dispatch => {
    const response = await fetchDataWithoutJWT(
        'auth/register',
        {
            name: user_name,
            email,
            password,
        },
        'POST'
    );

    const body = await response.json();
    const { ok, token, name, u_id } = body;

    if (ok) {
        localStorage.setItem('token', token);
        localStorage.setItem('token-init-date', new Date().getTime());

        dispatch(login({ name, u_id }));
    } else Swal.fire('Error', body?.msg, 'error');
};

export const startCheckingLoginState = () => async dispatch => {
    const response = await fetchDataWithJWT('auth/renew');

    const body = await response.json();
    const { ok, token, name, u_id } = body;

    if (ok) {
        localStorage.setItem('token', token);
        localStorage.setItem('token-init-date', new Date().getTime());

        dispatch(login({ name, u_id }));
    } else dispatch(finishCheckingLoginState());
};

const finishCheckingLoginState = () => ({
    type: types.AUTH_FINISH_CHECKING_LOGIN_STATE,
});

export const startLogout = () => dispatch => {
    localStorage.clear();

    dispatch(eventLogout());
    dispatch(logout());
};

const logout = () => ({ type: types.AUTH_LOGOUT });

const login = user => ({
    type: types.AUTH_LOGIN,
    payload: user,
});
