import types from '../types/types';

export const uiOpenModal = () => ({ type: types.UI_OPEN_MODAL });

export const uiCloseModal = () => ({ type: types.UI_CLOSE_MODAL });

export const uiSetError = (inputName, errorMsg) => ({
    type: types.UI_SET_ERROR,
    payload: {
        errorMsg,
        inputName,
    },
});
