import Swal from 'sweetalert2';

import types from '../types/types';

import { fetchDataWithJWT } from '../helpers/fetchData';
import { prepareEvents } from '../helpers/prepareEvents';

export const eventStartNew = calendarEvent => async (dispatch, getState) => {
    try {
        const { u_id, name } = getState().auth;
        const response = await fetchDataWithJWT(
            'events',
            calendarEvent,
            'POST'
        );

        const body = await response.json();
        const { ok, event } = body;
        const { id } = event;

        if (ok) {
            event.id = id;
            event.user = { u_id, name };

            dispatch(eventNew(event));
        } else Swal.fire('Error', body?.msg, 'error');
    } catch (error) {
        console.log(error);
    }
};

const eventNew = event => ({ type: types.EVENT_NEW, payload: event });

export const eventStartLoad = () => async dispatch => {
    try {
        const response = await fetchDataWithJWT('events');

        const body = await response.json();
        const { events } = body;

        const formatedEvents = prepareEvents(events);

        dispatch(eventLoad(formatedEvents));
    } catch (error) {
        console.log(error);
    }
};

const eventLoad = events => ({
    type: types.EVENT_LOAD,
    payload: events,
});

export const eventStartUpdate = event => async dispatch => {
    try {
        const response = await fetchDataWithJWT(
            `events/${event.id}`,
            event,
            'PUT'
        );

        const body = await response.json();
        const { ok } = body;

        if (ok) {
            dispatch(eventUpdate(event));
        } else Swal.fire('Error', body?.msg, 'error');
    } catch (error) {
        console.log(error);
    }
};

const eventUpdate = event => ({
    type: types.EVENT_UPDATE,
    payload: event,
});

export const eventStartDelete = () => async (dispatch, getState) => {
    try {
        const { id } = getState().event.active_event;

        const response = await fetchDataWithJWT(`events/${id}`, {}, 'DELETE');

        const body = await response.json();
        const { ok } = body;

        if (ok) {
            dispatch(eventDelete());
        } else Swal.fire('Error', body?.msg, 'error');
    } catch (error) {
        console.log(error);
    }
};

const eventDelete = () => ({ type: types.EVENT_DELETE });

export const eventLogout = () => ({ type: types.EVENT_LOGOUT });

export const eventSetActive = event => ({
    type: types.EVENT_SET_ACTIVE,
    payload: event,
});

export const eventClearActiveEvent = () => ({
    type: types.EVENT_CLEAR_ACTIVE_EVENT,
});
