import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSignOutAlt } from '@fortawesome/free-solid-svg-icons';
import { useDispatch, useSelector } from 'react-redux';

import { startLogout } from '../../actions/authActions';

const Navbar = () => {
    const { name } = useSelector(state => state.auth);
    const dispatch = useDispatch();

    const handleClick = () => dispatch(startLogout());

    return (
        <div className="navbar navbar-dark bg-dark mb-4">
            <div className="navbar-brand">
                <small className="d-block">Welcome</small>
                <span className="font-weight-bold h3">{name}</span>
            </div>

            <button className="btn btn-outline-light p-2" onClick={handleClick}>
                <FontAwesomeIcon icon={faSignOutAlt} />
                <span className="ml-1 font-weight-bold">Sign Out</span>
            </button>
        </div>
    );
};

export default Navbar;
