import React from 'react';
import validator from 'validator';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

import { startLogin } from '../../actions/authActions';

import useForm from '../../hooks/useForm';
import { uiSetError } from '../../actions/uiActions';

const LoginScreen = () => {
    const [formValues, handleChange] = useForm({
        email: 'bryan.mendoza@gmail.com',
        password: '123456',
    });

    const { error_msg, input_name } = useSelector(state => state.ui);
    const dispatch = useDispatch();

    const { email, password } = formValues;

    const handleSubmit = event => {
        event.preventDefault();

        if (!validator.isEmail(email) || validator.isEmpty(email)) {
            dispatch(uiSetError('email', 'Please type a valid email'));

            return;
        }

        if (
            !validator.isLength(password, { min: 6 }) ||
            validator.isEmpty(password)
        ) {
            dispatch(
                uiSetError('password', 'Passwords must be 6 characters long')
            );

            return;
        }

        dispatch(uiSetError(null, null));
        dispatch(startLogin(email, password));
    };

    return (
        <main className="min-vh-100 d-flex align-items-center">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-12 col-md-4">
                        <div className="card-body border rounded-lg">
                            <h1 className="font-weight-bold mb-4">Sign In</h1>

                            <form onSubmit={handleSubmit}>
                                <div className="form-group">
                                    <label
                                        className="col-form-label-sm font-weight-bold"
                                        htmlFor="email"
                                    >
                                        Email
                                    </label>
                                    <input
                                        type="email"
                                        id="email"
                                        name="email"
                                        className={
                                            input_name === 'email'
                                                ? 'form-control text-muted is-invalid'
                                                : 'form-control text-muted'
                                        }
                                        placeholder="john@example.com"
                                        value={email}
                                        onChange={handleChange}
                                    />
                                    {input_name === 'email' && (
                                        <div className="invalid-feedback">
                                            {error_msg}
                                        </div>
                                    )}
                                </div>

                                <div className="form-group">
                                    <label
                                        className="col-form-label-sm font-weight-bold"
                                        htmlFor="password"
                                    >
                                        Password
                                    </label>
                                    <input
                                        type="password"
                                        id="password"
                                        name="password"
                                        className={
                                            input_name === 'password'
                                                ? 'form-control text-muted is-invalid'
                                                : 'form-control text-muted'
                                        }
                                        value={password}
                                        onChange={handleChange}
                                    />
                                    {input_name === 'password' && (
                                        <div className="invalid-feedback">
                                            {error_msg}
                                        </div>
                                    )}
                                </div>

                                <button className="btn btn-outline-dark font-weight-bold btn-block mt-4 p-2">
                                    Sign In
                                </button>
                            </form>

                            <Link
                                className="mt-5 d-block font-weight-bold text-dark"
                                to="/auth/register"
                            >
                                Don't have an account?
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
};

export default LoginScreen;
