import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { uiCloseModal } from '../../actions/uiActions';
import {
    eventClearActiveEvent,
    eventStartNew,
    eventStartUpdate,
} from '../../actions/eventActions';

import Modal from 'react-modal';

import Swal from 'sweetalert2';

import DateTimePicker from 'react-datetime-picker';

import moment from 'moment';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSave } from '@fortawesome/free-solid-svg-icons';

import './CalendarModal.css';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    },
};

Modal.setAppElement('#root');

const now = moment().minutes(0).seconds(0).add(1, 'hour');
const later = now.clone().add(1, 'hour');

const initialEventData = {
    title: '',
    notes: '',
    start: now.toDate(),
    end: later.toDate(),
};

const CalendarModal = () => {
    const [titleValid, setTitleValid] = useState(true);

    const { modal_open } = useSelector(state => state.ui);
    const { active_event } = useSelector(state => state.event);

    const dispatch = useDispatch();

    const [formValues, setFormValues] = useState(initialEventData);

    const { notes, title, start, end } = formValues;

    useEffect(() => {
        active_event
            ? setFormValues(active_event)
            : setFormValues(initialEventData);
    }, [active_event]);

    const handleChange = ({ target }) => {
        setFormValues({
            ...formValues,
            [target.name]: target.value,
        });
    };

    const handleStartDate = event =>
        setFormValues({ ...formValues, start: event });

    const handleEndDate = event => setFormValues({ ...formValues, end: event });

    const closeModal = () => {
        dispatch(uiCloseModal());
        dispatch(eventClearActiveEvent());

        setFormValues(initialEventData);
    };

    const handleSubmit = event => {
        event.preventDefault();

        const momentStart = moment(start);
        const momentEnd = moment(end);

        if (momentStart.isSameOrAfter(momentEnd)) {
            Swal.fire(
                'Wait!',
                'The end date must be higher than the start date',
                'error'
            );

            return;
        }

        if (title.trim().length < 2) {
            setTitleValid(false);

            return;
        }

        setTitleValid(true);

        if (active_event) {
            dispatch(eventStartUpdate(formValues));
        } else {
            dispatch(eventStartNew(formValues));
        }

        closeModal();
    };

    return (
        <Modal
            className="modal rounded-lg"
            overlayClassName="modal--background"
            isOpen={modal_open}
            onRequestClose={closeModal}
            closeTimeoutMS={200}
            style={customStyles}
        >
            <h1 className="text-center font-weight-bold">
                {active_event ? 'Edit Event' : 'New Event'}
            </h1>
            <hr />

            <form className="container" onSubmit={handleSubmit}>
                <div className="form-group">
                    <label className="col-form-label-sm font-weight-bold">
                        Start Date and Time
                    </label>
                    <DateTimePicker
                        className="form-control"
                        onChange={handleStartDate}
                        value={start}
                        name="start_date"
                    />
                </div>

                <div className="form-group">
                    <label className="col-form-label-sm font-weight-bold">
                        End Date and Time
                    </label>
                    <DateTimePicker
                        className="form-control"
                        onChange={handleEndDate}
                        minDate={start}
                        value={end}
                        name="end_date"
                    />
                </div>

                <hr />

                <div className="form-group">
                    <label
                        className="col-form-label-sm font-weight-bold"
                        htmlFor="title"
                    >
                        Event Title
                    </label>

                    <input
                        className={`form-control ${
                            !titleValid && 'is-invalid'
                        }`}
                        type="text"
                        id="title"
                        name="title"
                        autoComplete="off"
                        value={title}
                        onChange={handleChange}
                    />
                    <small className="form-text text-muted">
                        A short description
                    </small>
                </div>

                <div className="form-group">
                    <label
                        className="col-form-label-sm font-weight-bold"
                        htmlFor="notes"
                    >
                        Event Notes
                    </label>
                    <textarea
                        className="form-control"
                        rows="5"
                        id="notes"
                        name="notes"
                        value={notes}
                        onChange={handleChange}
                    ></textarea>
                    <small className="form-text text-muted">
                        Aditional information
                    </small>
                </div>

                <button className="btn btn-outline-dark btn-block my-4 p-2">
                    <FontAwesomeIcon icon={faSave} />
                    <span className="ml-1 font-weight-bold">Save</span>
                </button>
            </form>
        </Modal>
    );
};

export default CalendarModal;
