const BASE_URL = process.env.REACT_APP_API_URL;

export const fetchDataWithoutJWT = (endpoint, data, method = 'GET') => {
    const URL = `${BASE_URL}/${endpoint}`;

    if (method === 'GET') {
        return fetch(URL);
    } else {
        return fetch(URL, {
            method,
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        });
    }
};

export const fetchDataWithJWT = (endpoint, data, method = 'GET') => {
    const URL = `${BASE_URL}/${endpoint}`;
    const token = localStorage.getItem('token') || '';

    if (method === 'GET') {
        return fetch(URL, {
            method,
            headers: {
                'x-token': token,
            },
        });
    } else {
        return fetch(URL, {
            method,
            headers: {
                'Content-Type': 'application/json',
                'x-token': token,
            },
            body: JSON.stringify(data),
        });
    }
};
