import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import validator from 'validator';
import { startRegister } from '../../actions/authActions';

import { uiSetError } from '../../actions/uiActions';

import useForm from '../../hooks/useForm';

const RegisterScreen = () => {
    const [formValues, handleChange] = useForm({
        name: 'Miguel',
        email: 'miguel@gmail.com',
        password: '123456',
        confirm_password: '123456',
    });

    const { error_msg, input_name } = useSelector(state => state.ui);
    const { name, email, password, confirm_password } = formValues;

    const dispatch = useDispatch();

    const handleSubmit = event => {
        event.preventDefault();

        if (validator.isEmpty(name)) {
            dispatch(uiSetError('name', "Name can't be empty"));

            return;
        }

        if (!validator.isAlpha(name)) {
            dispatch(uiSetError('name', 'Names can only contain letters'));

            return;
        }

        if (!validator.isEmail(email) || validator.isEmpty(email)) {
            dispatch(uiSetError('email', 'Please type a valid email'));

            return;
        }

        if (
            !validator.isLength(password, { min: 6 }) ||
            validator.isEmpty(password)
        ) {
            dispatch(
                uiSetError('password', 'Passwords must be 6 characters long')
            );

            return;
        }

        if (!validator.equals(password, confirm_password)) {
            dispatch(uiSetError('confirm_password', 'Passwords are not equal'));

            return;
        }

        dispatch(uiSetError(null, null));
        dispatch(startRegister(name, email, password));
    };

    return (
        <main className="min-vh-100 d-flex align-items-center">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-12 col-md-6">
                        <div className="card-body border rounded-lg">
                            <h1 className="font-weight-bold mb-4">Sign Up</h1>

                            <form onSubmit={handleSubmit}>
                                <div className="form-group">
                                    <label
                                        className="col-form-label-sm font-weight-bold"
                                        htmlFor="name"
                                    >
                                        Name
                                    </label>
                                    <input
                                        type="text"
                                        id="name"
                                        name="name"
                                        className={
                                            input_name === 'name'
                                                ? 'form-control text-muted is-invalid'
                                                : 'form-control text-muted'
                                        }
                                        placeholder="John"
                                        value={name}
                                        onChange={handleChange}
                                    />
                                    {input_name === 'name' && (
                                        <div className="invalid-feedback">
                                            {error_msg}
                                        </div>
                                    )}
                                </div>

                                <div className="form-group">
                                    <label
                                        className="col-form-label-sm font-weight-bold"
                                        htmlFor="email"
                                    >
                                        Email
                                    </label>
                                    <input
                                        type="email"
                                        id="email"
                                        name="email"
                                        className={
                                            input_name === 'email'
                                                ? 'form-control text-muted is-invalid'
                                                : 'form-control text-muted'
                                        }
                                        placeholder="john@example.com"
                                        value={email}
                                        onChange={handleChange}
                                    />
                                    {input_name === 'email' && (
                                        <div className="invalid-feedback">
                                            {error_msg}
                                        </div>
                                    )}
                                </div>

                                <div className="row">
                                    <div className="col-12 col-md-6">
                                        <div className="form-group">
                                            <label
                                                className="col-form-label-sm font-weight-bold"
                                                htmlFor="password"
                                            >
                                                Password
                                            </label>
                                            <input
                                                type="password"
                                                id="password"
                                                name="password"
                                                className={
                                                    input_name === 'password'
                                                        ? 'form-control text-muted is-invalid'
                                                        : 'form-control text-muted'
                                                }
                                                value={password}
                                                onChange={handleChange}
                                            />
                                            {input_name === 'password' && (
                                                <div className="invalid-feedback">
                                                    {error_msg}
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                    <div className="col-12 col-md-6">
                                        <div className="form-group">
                                            <label
                                                className="col-form-label-sm font-weight-bold"
                                                htmlFor="confirm_password"
                                            >
                                                Confirm Password
                                            </label>
                                            <input
                                                type="password"
                                                id="confirm_password"
                                                name="confirm_password"
                                                className={
                                                    input_name ===
                                                    'confirm_password'
                                                        ? 'form-control text-muted is-invalid'
                                                        : 'form-control text-muted'
                                                }
                                                value={confirm_password}
                                                onChange={handleChange}
                                            />
                                            {input_name ===
                                                'confirm_password' && (
                                                <div className="invalid-feedback">
                                                    {error_msg}
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                </div>

                                <button className="btn btn-outline-dark font-weight-bold btn-block mt-2 p-2">
                                    Sign Up
                                </button>
                            </form>

                            <Link
                                className="mt-5 d-block font-weight-bold text-dark"
                                to="/auth/login"
                            >
                                Have an account already?
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
};

export default RegisterScreen;
