import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { uiOpenModal } from '../../actions/uiActions';
import {
    eventClearActiveEvent,
    eventSetActive,
    eventStartLoad,
} from '../../actions/eventActions';

import { Calendar, momentLocalizer } from 'react-big-calendar';
import 'react-big-calendar/lib/css/react-big-calendar.css';

import DeleteEventFAB from '../ui/DeleteEventFAB';

import moment from 'moment';

import Navbar from '../ui/Navbar';
import CalendarEvent from './CalendarEvent';
import CalendarModal from './CalendarModal';
import NewEventFAB from '../ui/NewEventFAB';

const localizer = momentLocalizer(moment);

const CalendarScreen = () => {
    const [lastView, setLastView] = useState(
        localStorage.getItem('lastView') || 'month'
    );

    const dispatch = useDispatch();
    const { events, active_event } = useSelector(state => state.event);
    const { u_id } = useSelector(state => state.auth);

    const onDoubleClick = () => dispatch(uiOpenModal());

    const onSelect = event => dispatch(eventSetActive(event));

    const onSelectSlot = () => dispatch(eventClearActiveEvent());

    const onViewChange = event => {
        setLastView(event);

        localStorage.setItem('lastView', event);
    };

    const eventStyleGetter = (event, start, end, isSelected) => {
        const style = {
            backgroundColor: u_id === event.user._id ? '#367cf7' : '#465660',
            borderRadius: 0,
            border: 'none',
            opacity: 0.8,
            display: 'block',
            color: '#fff',
        };

        return { style };
    };

    useEffect(() => {
        dispatch(eventStartLoad());
    }, [dispatch]);

    return (
        <div className="calendar-screen">
            <Navbar />

            <Calendar
                localizer={localizer}
                events={events}
                startAccessor="start"
                endAccessor="end"
                eventPropGetter={eventStyleGetter}
                onDoubleClickEvent={onDoubleClick}
                onSelectEvent={onSelect}
                onSelectSlot={onSelectSlot}
                selectable={true}
                onView={onViewChange}
                view={lastView}
                components={{ event: CalendarEvent }}
            />

            <NewEventFAB />

            {active_event && <DeleteEventFAB />}

            <CalendarModal />
        </div>
    );
};

export default CalendarScreen;
